import React from 'react';
import moment from 'moment'
import utils from '../services/utils'
import categories from '../categories'

import { makeStyles } from '@material-ui/core/styles';
import { Card, CardContent, CardMedia, Typography, Link, CardActionArea } from '@material-ui/core';
import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    width: "100%",
    
    maxHeight: "235px",
    minHeight: "235px",
  },
  sourceTime: {
    paddingRight: "1em"
  },
  sourceLink: {
    color: theme.typography.subtitle1.color
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
    width:"100%",
    paddingTop: "0.75em"
  },
  content: {
    paddingTop: 0,
    flex: '1 0 auto',
  },
  cover: {
    maxWidth: 300,
  },
  verticalBar : {
    minWidth: '0.3em', 
    height:"4em", 
    marginTop : "1.5em",
  },
  cardArea: {
    display: "inherit",
    alignItems: "stretch"
  }
}));

export default function HorizontalNewsCard(props) {
  const classes = useStyles();
  
  const isOnlyText = !props.imageUrl;
  const articleDate = new Date(props.pubDateTime).toString();
  const readableDate = moment(new Date(articleDate)).format('LLLL');
  const relativeDate = moment(new Date(articleDate)).fromNow();

  return (
    <Card className={clsx(classes.root, props.className)}>
          <div className={classes.verticalBar} style={{backgroundColor : categories[props.category || categories.latest.title]?.color}}>
            </div>
      <CardActionArea className={classes.cardArea} onClick={(event) => utils.openInNewTab(props.articleUrl, event)}>
      <div className={classes.details}>
        <div className={classes.sourceTime}>
            <Link className={clsx(classes.sourceLink, "clickable")} variant="subtitle1" display='inline' 
            onClick={(event) => utils.openInNewTab(props.sourceUrl, event)}>{props.sourceTitle}</Link>
            <Typography  variant="subtitle1" display='inline' >&nbsp;-</Typography>
            <Typography  variant="subtitle1"  display='inline' title={readableDate}> {relativeDate}</Typography>
        </div>
        <CardContent className={classes.content}>
          <Typography component="h5" variant="h5">
          {props.title}
          </Typography>
          <Typography variant="subtitle1" color="textSecondary">
            {props.articleSummary? utils.toSnippet(props.articleSummary, 400) : ""}
          </Typography>
        </CardContent>
      </div>
      
      {isOnlyText ? undefined : (<CardMedia
        onClick={(event) => utils.openInNewTab(props.articleUrl, event)}
        className={clsx(classes.cover, 'clickable')}
        image={props.imageUrl}
        alt={props.title}
        component="img"
        referrerPolicy="no-referrer"
      />)}
      </CardActionArea>
    </Card>
  );
}

