import React from 'react';
import News from './components/News/News'

import './App.scss'
import theme from "./theme"
import {ApolloProvider} from '@apollo/client';
import { ApolloClient, InMemoryCache } from '@apollo/client';
import { relayStylePagination } from "@apollo/client/utilities";
import { ThemeProvider } from '@material-ui/core';
import ResponsiveDrawerLayout from './components/ResponsiveDrawerLayout';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import categories from './categories';


// init apollo client
const client = new ApolloClient({
  uri: process.env.REACT_APP_API_URL,
  cache: new InMemoryCache({
    typePolicies: {
      Query: {
        fields: {
          articles: relayStylePagination([]),
        },
      },
    },
  })
});

function App() {
    return (
        <ThemeProvider theme={theme}>
        <ApolloProvider client={client}>
          <BrowserRouter>
            <div>
                <Switch>
                <Route path='/:category'  render = { routerProps => {
                  const selectedCategory = routerProps?.match?.params?.category ||'latest';
                  return getLayout(selectedCategory)
                }}/>
                <Route path='/'  render = { routerProps => {
                  return getLayout(categories.latest.title)
                }}/>
                </Switch>
            </div>
            </BrowserRouter>
        </ApolloProvider>
        </ThemeProvider>
    );
}

function getLayout(category) {
  return (<ResponsiveDrawerLayout category={category}>
    <News category ={category}/>
    </ResponsiveDrawerLayout>)
}

export default App;
