import React from 'react';
import { makeStyles} from '@material-ui/core/styles';
import { Link, Card, CardActionArea, CardContent, CardMedia, Typography } from '@material-ui/core';
import moment from 'moment'
import utils from '../services/utils'
import categories from '../categories'
import clsx from 'clsx';


const useStyles = makeStyles((theme) => ({
  flexRow: {
    display: "flex",
    flexDirection: "row",
    padding: 0
  },
  sourceTime: {
    paddingTop: "0.5em"
  },
  sourceLink: {
    color: theme.typography.subtitle1.color
  },
  verticalBar : {
    minWidth: '0.3em', 
    maxWidth: '0.3em', 
    height:"4em", 
    marginTop : "1.5em",
  },
  content: {
    padding: "1em"
  }
}));

export default function VerticalNewsCard(props) {
  const classes = useStyles();
  const articleDate = new Date(props.pubDateTime).toString();
  const readableDate = moment(new Date(articleDate)).format('LLLL');
  const relativeDate = moment(new Date(articleDate)).fromNow();

  return (
    <Card className={clsx(classes.root, props.className)}>
      <CardActionArea onClick={(event) => utils.openInNewTab(props.articleUrl, event)}>
        {props.imageUrl ? <CardMedia
          component="img"
          alt={props.title}
          height="250"
          image={props.imageUrl}
          title={props.title}
          referrerPolicy="no-referrer"
        /> : undefined}
        <CardContent className={classes.flexRow}>
                    <div className={classes.verticalBar} style={{backgroundColor : categories[props.category || categories.latest.title]?.color}}>
                        </div>
                        <div className={classes.content}>
          <Typography gutterBottom variant="h5" component="h2">
            {props.title}
          </Typography>
          <Typography variant="subtitle1" color="textSecondary" component="p">
            {utils.toSnippet(props.articleSummary, 250)}
          </Typography>
          <div className={classes.sourceTime}>
                <Link className={clsx("clickable", classes.sourceLink)} variant="subtitle1" display='inline' 
                onClick={(event) => utils.openInNewTab(props.sourceUrl, event)}>{props.sourceTitle}</Link>
                <Typography  variant="subtitle1" display='inline' >&nbsp;-</Typography>
                <Typography  variant="subtitle1" display='inline' title={readableDate}> {relativeDate}</Typography>
            </div>
          </div>
        </CardContent>
      </CardActionArea>
    </Card>
  );
}
