import React from 'react';
import './News.scss'
import categories from '../../categories'

import {makeStyles} from '@material-ui/core/styles';
import {Grid, CircularProgress, Typography} from '@material-ui/core';
import {KeyboardArrowDown, ErrorOutline} from '@material-ui/icons';
import HorizontalNewsCard from '../HorizontalNewsCard';
import VerticalNewsCard from '../VerticalNewsCard';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '95%',
        margin:"auto",
    },
    categoryTitle: {
        margin : "0.25em",
    },
    main: { 
        [theme.breakpoints.down('xs')]: {
            display : "none",
        },
    },
    alternative : {
        display : "none",
        [theme.breakpoints.down('xs')]: {
            display : "revert",
        },
    },
    newsCardsContainer : {
        marginBottom: "2em"
    },
    verticalBar: {
        minWidth: '0.3em', 
        marginTop: "1.2em", 
        height:"4em",
    }
}));

function NewsPresenation(props) {

    const {loading, error, articles, loadMore, hasNextPage, category} = props;
    const classes = useStyles();
    return (
        <div className={classes.root}>
            
            {getCategoryNews(category, articles, classes)}

            <Grid container justify="center">
                {loading ? (<CircularProgress />)
                : (<div className="load-more clickable"
                        onClick={() => hasNextPage && !loading ? loadMore() : undefined}>
                    
                            {error ?  (<ErrorOutline fontSize="large"  color="secondary"/>)
                            :(<KeyboardArrowDown color="primary" fontSize="large"/>)}
                        
                    </div>)
                }
            </Grid>

        </div>
    )
}

function getCategoryNews(title, articles, classes) {
    return (<div className={classes.newsCardsContainer}>
            <div style={{display: "flex", direction: "row"}}>
                <div className={classes.verticalBar} style={{backgroundColor : categories[title]?.color}}>
                </div>
                <Typography className={classes.categoryTitle}  variant="h2" component="div">
                {categories[title]?.displayName}
                </Typography>
            </div>
            <Grid container className={classes.newsCards} spacing={2}>

                {articles.map((article) => (
                        <Grid key ={article.id} className={classes.newsCard} item xs={12} md = {12}  lg={10}>
                            <HorizontalNewsCard className={classes.main}  {...article}></HorizontalNewsCard>
                            <VerticalNewsCard className={classes.alternative}  {...article}></VerticalNewsCard>
                        </Grid>
                    ))}

            </Grid>
        </div>)
}

export default NewsPresenation
