import React from 'react';
import PropTypes from 'prop-types';
											  
import AppBar from '@material-ui/core/AppBar';
import { useHistory } from "react-router-dom";
													  
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Divider, Drawer, Hidden, IconButton, Toolbar, List, ListItem, ListItemIcon, ListItemText, CssBaseline } from '@material-ui/core';
													   
import { Menu as MenuIcon } from '@material-ui/icons';
import categories from '../categories'

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex'
  },
  drawer: {
    [theme.breakpoints.up('md')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    backgroundColor: 'white'
  },
  menuButton: {
    marginRight: theme.spacing(1),
    marginLeft: theme.spacing(1),
    color: "black",
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
  listItem: {
    textAlign: 'right'
  }
}));

function ResponsiveDrawerLayout(props) {
  const { window, match } = props;
  const selectedCategory = props.category || categories[match?.params?.category]?.title || categories.latest.title;
  const classes = useStyles();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const history = useHistory();

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };
  const gotoRoute = (category) => {
    if(mobileOpen) {
      handleDrawerToggle();
    }
    history.push(`/${category}`);
  };

  const getMenuItems = (categoriesList) => (
    <List>
        {categoriesList.map((cat, index) => (
          <ListItem button key={categories[cat].displayName} className={classes.listItem}
            style={cat === selectedCategory ? {backgroundColor : categories[cat].highlightColor} : {}}
            onClick={()=> gotoRoute(cat)}
          >
            <ListItemIcon style= {{color : categories[cat].color}}>{categories[cat].icon}</ListItemIcon>
            <ListItemText primary={categories[cat].displayName} />
          </ListItem>
        ))}
      </List>
  )

  const drawer = (
    <div>
      <div className={classes.toolbar} />
      <Divider />
        {getMenuItems(categories.otherList)}
      <Divider />
      <List>
      {getMenuItems(categories.list)}
      </List>
    </div>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar style={{padding: 0}}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <img className={classes.toolbar} src={process.env.PUBLIC_URL + 'logo.png'} alt="Logo"
            style={{maxHeight: 64,     marginRight: theme.spacing(2) }}
            onClick={()=>gotoRoute('')}
          />

        </Toolbar>
      </AppBar>
			 
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden mdUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden smDown className="test" implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
      <main className={classes.content}>
        
      <div className={classes.toolbar} />
            {props.children}
      </main>
    </div>
  );
}
//<News category={selectedCategory}/>

ResponsiveDrawerLayout.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default ResponsiveDrawerLayout;
